import '../../types.d';
// @ts-ignore
import html from './template.html'

import { addValidation } from '../../shared/validation';
import { registerMessages } from '../../shared/translations';
import * as i18n from './i18n';
import { convertCharsToHalfWidth } from '../../shared/char-width-utils';
registerMessages(i18n);

export const render: KomojuRenderFunction = (root, paymentMethod) => {
  root.innerHTML = html;

  // All fields are required.
  root.querySelectorAll('input').forEach(element => {
    addValidation(i18n, element, (input) => {
      if (input.value === '') return 'bt.error.required';
      return null;
    });
  });

  // Automatically convert full-width kana to half-width.
  root.querySelectorAll('.kana').forEach(element => {
    element.addEventListener('input', (event) => {
      const input = event.target as HTMLInputElement;

      // This "ime" data comes from komoju-host-element.ts. It lets us avoid messing up
      // customer input during IME composition.
      if (input.dataset.ime === 'active') return;

      input.value = convertCharsToHalfWidth(input.value);
    });
  });
}

export const paymentDetails: KomojuPaymentDetailsFunction = (root, _paymentMethod) => {
  const firstname = root.querySelector('#bt-firstname')! as HTMLInputElement;
  const lastname = root.querySelector('#bt-lastname')! as HTMLInputElement;
  const firstnameKana = root.querySelector('#bt-firstname-kana')! as HTMLInputElement;
  const lastnameKana = root.querySelector('#bt-lastname-kana')! as HTMLInputElement;
  const email = root.querySelector('#bt-email')! as HTMLInputElement;
  const phone = root.querySelector('#bt-phone')! as HTMLInputElement;

  return {
    type: 'bank_transfer',
    email: email.value,
    family_name: lastname.value,
    family_name_kana: lastnameKana.value,
    given_name: firstname.value,
    given_name_kana: firstnameKana.value,
    phone: phone.value,
  }
}
